@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;
.p-result-details {
  padding: 7rem 0 15.6rem 0;
  @include sp {
    padding: 4.2rem 0 19.6rem 0;
  }
  &__image {
    position: relative;
    &.hops {
      &::before {
        content: "ホップス";
        position: absolute;
        left: 0;
        top: 0;
        background-color: rgb(203, 0, 0);
        color: #ffffff;
        width: 10rem;
        height: 3rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        line-height: calc(25 / 15);
        font-weight: bold;
      }
    }
    img {
      aspect-ratio: 11 / 5;
      object-fit: cover;
      @include sp {
        aspect-ratio: 335 / 236;
      }
    }
  }
  &__title {
    margin-top: 5rem;
    margin-top: 4.3rem;
    @include sp {
      margin-top: 2.7rem;
    }
    h1 {
      font-size: 2.5rem;
      line-height: calc(40 / 25);
      font-weight: bold;
      @include sp {
        font-size: 2rem;
        line-height: calc(30 / 20);
      }
    }
  }
  &__date {
    display: inline-block;
    margin-top: 1.5rem;
    @include sp {
      margin-top: 0.6rem;
    }
    time {
      font-size: 1.7rem;
      line-height: calc(30 / 17);
    }
  }
  &__profile-inner {
    margin-top: 6rem;
    display: flex;
    column-gap: 4rem;
    @include sp {
      flex-direction: column;
      margin-top: 2.1rem;
    }
  }
  &__profile-data {
    table {
      tr {
        // display: flex;
        // align-items: center;
      }
      th {
        background: rgba(229, 229, 229, 1);
        border-top: 0.1rem solid rgba(112, 112, 112, 1);
        border-bottom: 0.1rem solid rgba(112, 112, 112, 1);
        border-left: 0.1rem solid rgba(112, 112, 112, 1);
        width: 14rem;
        height: calc(190 / 5) * 0.1rem;
        font-weight: bold;
        font-size: 1.8rem;
        line-height: calc(30 / 18);
        padding-left: 2rem;
        vertical-align: middle;
        @include sp {
          // padding: 0.8rem 1rem 0;
          padding: 0 1rem;
        }
      }
      td {
        border-top: 0.1rem solid rgba(112, 112, 112, 1);
        border-right: 0.1rem solid rgba(112, 112, 112, 1);
        border-left: 0.1rem solid rgba(112, 112, 112, 1);
        width: 26rem;
        height: calc(190 / 5) * 0.1rem;
        font-weight: 400;
        font-size: 1.8rem;
        line-height: calc(30 / 18);
        padding-left: 2rem;
        vertical-align: middle;
        // padding: 0.8rem 8rem 0 2rem;

        &:last-child {
          border-bottom: 0.1rem solid rgba(112, 112, 112, 1);
        }
      }
    }
  }
  &__profile-comment {
    width: 44rem;
    @include sp {
      width: 100%;
      margin-top: 2.2rem;
    }
    p {
      font-size: 1.8rem;
      line-height: calc(30 / 18);
    }
  }
  &-main__articles {
    // margin-top: 5.8rem;
    margin-top: 6.3rem;
    @include sp {
      margin-top: 4.3rem;
    }
    .p-paging {
      @include sp {
        column-gap: initial;
        display: flex;
        column-gap: 2rem;
      }
      // ページ部分
      &-item__heading {
        // 前のページ
        &-prev {
          padding-left: 2rem;
        }
        // 前のページ
        &-next {
          padding-right: 1.8rem;
          @include sp {
            padding-left: 4.6rem;
          }
          p {
            text-align: right;
            width: 100%;
          }
        }
      }
    }
  }
  .page-relation {
    margin-top: 10rem;
    display: grid;
    row-gap: 3rem;
    @include sp {
      margin-top: 6.7rem;
      row-gap: 2rem;
    }
    // 関連記事タイトル
    &__title {
      display: flex;
      align-items: center;
      padding-inline-start: 3rem;
      height: 5rem;
      @include sp {
        padding-inline-start: initial;
      }
      h2 {
        font-weight: bold;
        font-size: 1.8rem;
        line-height: calc(33 / 18);
        @include sp {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 100%;
        }
      }
    }
    &__items {
      row-gap: 3rem;
      display: grid;
      @include sp {
        display: flex;
        flex-direction: column;
        row-gap: 2rem;
      }
    }
    &__item {
      display: flex;
      column-gap: 3rem;
      @include sp {
        column-gap: 2rem;
      }
    }
    &__image {
      display: flex;
      justify-content: center;
      position: relative;
      width: 26rem;
      flex-shrink: 0;
      @include sp {
        width: 16rem;
      }
      &::before {
        position: absolute;
        left: 0;
        top: 0;
        background-color: rgb(203, 0, 0);
        color: #ffffff;
        width: 10rem;
        height: 3rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: calc(25 / 15);
        @include sp {
          width: 8rem;
        }
      }
      // ロック
      &.rock {
        &::before {
          content: "ロック";
        }
      }
      // ジャズ
      &.jazz {
        &::before {
          content: "ジャズ";
        }
      }
      // ラテン
      &.latin {
        &::before {
          content: "ラテン";
        }
      }
      img {
        object-fit: cover;
        aspect-ratio: 260 / 183;
        @include sp {
          aspect-ratio: 160 / 113;
        }
      }
    }
    &__text-inner {
      display: flex;
      flex-direction: column;
      row-gap: 1.3rem;
      @include sp {
        row-gap: 0.6rem;
      }
      p {
        font-weight: bold;
        font-size: 2rem;
        line-height: calc(25 / 20);
        @include sp {
          font-size: 1.7rem;
          line-height: calc(28 / 17);
        }
      }
      &__title-text {
        font-weight: bold;
        font-size: 2rem;
        line-height: calc(25 / 20);
        @include sp {
          display: none;
        }
      }
      .pc-only {
        @include sp {
          display: none;
        }
      }
      .sp-only {
        display: none;
        @include sp {
          display: block;
        }
      }

      time {
        display: block;
        padding-left: 0.3rem;
        font-size: 1.7rem;
        line-height: calc(30 / 17);
      }
    }
  }
}
